import { MemoryRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import React, { useEffect, useState } from 'react';
import { Layout, Menu, Watermark, MessagePlugin } from 'tdesign-react';
import 'tdesign-react/es/style/index.css'; // 少量公共样式
import { InfoCircleFilledIcon, SettingIcon, WifiIcon } from 'tdesign-icons-react'
const { Content, Footer, Aside } = Layout;
const { MenuItem } = Menu;

import './App.css';

import Home from './Home';
import Settings from './Settings'
import Network from './Network'

console.log('Powered by Ar-Sr-Na');
const { ipcRenderer } = window.electron;


const Logo = () => <img height="40" style={{ padding: 5 }} src={require('./logo.png')} alt="logo" />;
function SideBar() {
  const navigate = useNavigate();
  return (
    <>
      <Menu style={{ width: '100%', height: '100%', boxShadow: 'none' }} defaultValue='/' logo={<><Logo /><h3>ArRM 从机端</h3></>}
        onChange={navigate}
      >
        <MenuItem value="/" icon={<InfoCircleFilledIcon />}>状态</MenuItem>
        <MenuItem value="/network" icon={<WifiIcon />}>网络信息</MenuItem>
        <MenuItem value="/settings" icon={<SettingIcon />}>设置</MenuItem>
      </Menu >
    </>
  )
}


function Main() {
  const navigate = useNavigate();
  const settings = localStorage.settings == null ? [] : JSON.parse(localStorage.settings);
  const [status, setStatus] = useState(false);
  const [client, setClient] = useState(null);

  useEffect(() => {
    if (localStorage.settings == null) {
      MessagePlugin.info('首次使用，请先完成设置后重启！')
      navigate('/settings');
    } else {
      ipcRenderer.sendMessage('startServer', settings);
      ipcRenderer.once('startServer', m => { setStatus(m.success) });
    }

    ipcRenderer.on('client', m => { setClient(m) })
  }, [])
  return (
    <><Watermark watermarkContent={{ text: 'Ar-Sr-Na 测试版本', fontColor: 'rgba(100,100,100,.1)' }} y={120} x={80}>
      <Layout style={{ minHeight: '100vh' }}>
        <Aside style={{ position: 'fixed', height: '100%' }}>
          <SideBar />
        </Aside>
        <Layout>
          <Content style={{ padding: 20, marginLeft: 240 }}>
            <Routes>
              <Route path='/' element={<Home settings={settings} status={status} client={client} />} />
              <Route path='/settings' element={<Settings />} />
              <Route path='/network' element={<Network />} />
            </Routes>
          </Content>
          <Footer>Powered by Ar-Sr-Na</Footer>
        </Layout>
      </Layout>
    </Watermark >
    </>
  )
}

export default function App() {
  return (
    <Router>
      <Main />
    </Router>
  );
}

function CheckUpdate() {
  const count = 1
  const [show, setShow] = useState(true);
  const [info, setInfo] = useState({});

  useEffect(() => {
    fetch('https://api.arsrna.cn/release/appUpdate/ArRM_C')
      .then(msg => msg.json())
      .then(msg => {
        console.log(msg);
        setInfo(msg);
        if (msg.count <= count) { setTimeout(() => setShow(false), 3000) }
      });
  }, [])


  return (
    <ToastContainer position='top-end' style={{ padding: 30 }}>
      <Toast show={show} onClose={() => setShow(false)}>
        <Toast.Header>
          <InfoCircle className='me-2' />
          <strong className="me-auto">更新提醒</strong>
          <small className='text-muted'>版本号 {info.vNumber}</small>
        </Toast.Header>
        <Toast.Body>{
          info.count > count ?
            (<>有新版本，请<a href={info.link} target='_blank'>点此前往下载</a></>)
            : '当前已是最新版本'
        }</Toast.Body>
      </Toast>
    </ToastContainer>
  )
}